* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 30px;
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: grey;
}

.card {
  border: 2px;
  border-style: solid;
  padding: 2px;
  margin: 10px;
}

.card p {
  text-align: center;
}

.flex-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center
}